import { faStar, faStarHalfAlt } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react'
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

function Testimonial() {
    return (
        <>
            <section className="testimonial-section spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="section-title">
                                <span>Testimonials</span>
                                <h2>What Customers Say?</h2>
                            </div>
                        </div>
                    </div>

                    <Carousel
                        additionalTransfrom={0}
                        arrows
                        autoPlay
                        autoPlaySpeed={3000}
                        centerMode={false}
                        className=""
                        containerclassName="container-with-dots"
                        dotListclassName=""
                        draggable
                        focusOnSelect={false}
                        infinite
                        itemClassName="carousel-item-padding-40-px"
                        keyBoardControl
                        minimumTouchDrag={80}
                        pauseOnHover
                        renderArrowsWhenDisabled={false}
                        renderButtonGroupOutside={false}
                        renderDotsOutside={false}
                        responsive={{
                            desktop: {
                                breakpoint: {
                                    max: 6000,
                                    min: 1500
                                },
                                items: 1,
                                partialVisibilityGutter: 40
                            },
                            mobile: {
                                breakpoint: {
                                    max: 1499,
                                    min: 1200
                                },
                                items: 1,
                                partialVisibilityGutter: 30
                            },
                            tablet: {
                                breakpoint: {
                                    max: 1024,
                                    min: 464
                                },
                                items: 1,
                                partialVisibilityGutter: 30
                            }
                        }}
                        rewind={false}
                        rewindWithAnimation={false}
                        rtl={false}
                        shouldResetAutoplay
                        showDots={false}
                        sliderclassName=""
                        slidesToSlide={1}
                        swipeable
                        itemClass="carousel-item-padding-40-px"
                    >
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2">
                                <div class="testimonial-slider owl-carousel">
                                    <div className="ts-item">
                                        <p>"A hidden gem tucked away in the heart of nature.
                                            Our stay at the Rustic Villa Hotel was nothing short of magical.
                                            From the warm hospitality to the stunning views, every moment felt like a dream come true.
                                            Can't wait to return!"</p>
                                        <div className="ti-author">
                                            <div className="rating">
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStarHalfAlt} />
                                            </div>
                                            <h5> - Emily D.</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2">
                                <div class="testimonial-slider owl-carousel">
                                    <div className="ts-item">
                                        <p>An unforgettable escape from the ordinary.
                                            The Rustic Villa Hotel captured our hearts with its charming atmosphere and serene surroundings.
                                            The cozy accommodations, delectable cuisine, and attentive staff made our stay truly exceptional.
                                            Highly recommend!</p>
                                        <div className="ti-author">
                                            <div className="rating">
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStarHalfAlt} />
                                            </div>
                                            <h5> - James and Sarah M.</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2">
                                <div class="testimonial-slider owl-carousel">
                                    <div className="ts-item">
                                        <p>Paradise found! Our weekend getaway to the Rustic Villa Hotel exceeded all expectations.
                                            The peaceful ambiance, luxurious amenities, and picturesque setting provided the perfect backdrop for relaxation and rejuvenation.
                                            Already planning our next visit!</p>
                                        <div className="ti-author">
                                            <div className="rating">
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStarHalfAlt} />
                                            </div>
                                            <h5> - Alex and Jessica S.</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2">
                                <div class="testimonial-slider owl-carousel">
                                    <div className="ts-item">
                                        <p>A slice of heaven on earth. Words cannot express the beauty and tranquility of the Rustic Villa Hotel.
                                            From the moment we arrived, we were greeted with warmth and hospitality that made us feel right at home.
                                            A truly magical experience that we will cherish forever.</p>
                                        <div className="ti-author">
                                            <div className="rating">
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStarHalfAlt} />
                                            </div>
                                            <h5> - Michael and Lauren T.</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-8 offset-lg-2">
                                <div class="testimonial-slider owl-carousel">
                                    <div className="ts-item">
                                        <p>Escape to paradise at the Rustic Villa Hotel.
                                            Our stay was nothing short of perfection. The rustic charm, impeccable service, and breathtaking views made it a getaway we'll never forget.
                                            Thank you for an unforgettable experience!
                                        </p>
                                        <div className="ti-author">
                                            <div className="rating">
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStar} />
                                                <FontAwesomeIcon className="icon_star" icon={faStarHalfAlt} />
                                            </div>
                                            <h5> - David and Emma C.</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel>
                </div>
            </section >
        </>
    )
}

export default Testimonial