import React from 'react';
import mainGateImg from '../Assets/View/main-gate.jpeg';
import cafeteriaImg from '../Assets/View/Cafeteria.jpg';
import { Link } from 'react-router-dom';

function AboutSection() {
    return (
        <>
            <section className="aboutus-section spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="about-text">
                                <div className="section-title">
                                    <span>About Us</span>
                                    <h2>Discover Serenity in Nature's Embrace</h2>
                                </div>
                                <p className="f-para">Escape to rustic elegance at our villa hotel nestled in nature's embrace. Experience tranquility, comfort, and culinary delights amidst breathtaking surroundings. Book your getaway today and indulge in the beauty of simple joys.</p>
                                <p className="s-para">So when it comes to booking the perfect hotel, vacation rental, resort,
                                    apartment, guest house, or tree house, weâ€™ve got you covered.</p>
                                <Link to="/about-us" className="primary-btn about-btn">Read More</Link>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="about-pic">
                                <div className="row">
                                    <div className="col-sm-6">
                                        <img src={mainGateImg} alt="" />
                                    </div>
                                    <div className="col-sm-6">
                                        <img src={cafeteriaImg} alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default AboutSection